body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.page__wrapper {
  display: flex; }

.app__header {
  border-bottom: 1px solid black; }

.app__aside {
  width: 250px;
  border-right: 1px solid black; }

.aside__list {
  list-style: none; }

.aside__item-link {
  text-decoration: none; }

.app__container {
  display: flex;
  flex-direction: column;
  width: 100%; }

.app__main {
  padding-left: 10px; }

.breadcrumb__container {
  text-align: center; }

.breadcrumb__container-list {
  display: flex;
  flex-direction: row;
  list-style: none; }

.panel__session {
  display: flex;
  align-items: center; }

.icon {
  font-size: 18px; }

.panel__session-title {
  padding-left: 10px;
  font-size: 18px; }

.search__container {
  display: flex;
  align-items: center; }

.search__info-total {
  padding-left: 10px; }

.table__container {
  display: flex; }

.table__content {
  display: flex;
  flex-direction: row;
  align-items: center; }

.table__icons {
  display: flex;
  flex-direction: column;
  background-color: white; }

.app__footer-session {
  display: flex;
  justify-content: space-between;
  border-top: 1px solid black;
  padding-left: 10px; }

.pagination {
  list-style: none;
  display: flex;
  flex-direction: row;
  justify-content: center; }

